.footer{
    width: 100%;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 3rem;
    font-family: "lato", sans-serif;
}

.footer .list-items{
    padding-top: 3rem;
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10rem;
}

.footer .list-items .list{
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
    font-size: 1.2rem;
}

.icons{
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
    padding-left: 4rem;
}

.list a{
    text-decoration-line: none;
    color: black;
}

.list a:hover{
    text-decoration-line: underline;
}

.facebook{
    color: black;
}

.insta{
    color: black;
}

.twitter{
    color: black;
}

.facebook:hover{
    color: #1877F2;
    cursor: pointer;
    rotate: -35deg;
    transition: 0.25s;
}

.twitter:hover{
    color: #1DA1F2;
    cursor: pointer;
    rotate: -35deg;
    transition: 0.25s;
}

.insta:hover{
    color: #cd486b;
    cursor: pointer;
    rotate: -35deg;
    transition: 0.25s;
}

@media(max-width: 1110px){
    .footer .list-items{
        gap: 2.25rem;
    }
    
    .footer .list-items .icons{
        font-size: 1rem;
        padding: 0;
    }

    .footer .list-items .list{
        font-size: 1rem;
    }

    .facebook{
        width: 2rem;
    }

    .twitter{
        width: 2rem;
    }

    .insta{
        width: 2rem;
    }
}

@media(max-width: 600px){
    .footer .list-items{
        gap: 2rem;
    }

    .footer .list-items .icons{
        font-size: 0.75rem;
    }

    .footer .list-items .list{
        font-size: 0.75rem;
    }

    .facebook{
        width: 1.5rem;
    }

    .twitter{
        width: 1.5rem;
    }

    .insta{
        width: 1.5rem;
    }

    .copyright{
        font-size: 0.85rem;
    }
}

@media(max-width: 425){
    .footer .list-items{
        gap: 1rem;
    }

    .footer .list-items .icons{
        font-size: 0.5rem;
    }

    .footer .list-items .list{
        font-size: 0.5rem;
    }

    .facebook{
        width: 1.25rem;
    }

    .twitter{
        width: 1.25rem;
    }

    .insta{
        width: 1.25rem;
    }

    .copyright{
        font-size: 0.65rem;
    }
}

@media(max-width: 375px){
    .footer .list-items{
        gap: 1rem;
    }

    .footer .list-items .icons{
        font-size: 0.5rem;
    }

    .footer .list-items .list{
        font-size: 0.5rem;
    }

    .facebook{
        width: 1.1rem;
    }

    .twitter{
        width: 1.1rem;
    }

    .insta{
        width: 1.1rem;
    }
    .copyright{
        font-size: 0.55rem;
    }
}