.service-cards{
    width: 27%;
    height: 100%;
    border: 1px solid black;
    padding: 2rem;
}

.service-cards h2{
    font-family: "lato", sans-serif;
    font-size: 1.25rem;
}

.service-cards p{
    font-family: 'Noto Sans', sans-serif;
    font-size: 1rem;
}

@media(max-width: 1023px){
    .service-cards{
        width: 70%;
    }

}
