.shop-feature-cards{
    width: 25%;
    height: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.shop-feature-cards-link{
    display: flex;
    justify-content: center;
    align-items: center;
}

.shop-feature-cards h4{
    font-size: 2rem;
    font-family: "lato", sans-serif;
}

.shop-feature-cards p{
    font-weight: 2rem;
}

.shop-feature-cards div{
    font-size: 2.5rem;
    font-family: 'Bebas Neue', sans-serif;   
}

.shop-feature-cards div a{
    color: black;
    text-decoration-line: underline;
}

.shop-feature-cards div a:hover{
    color: red; 
    transition: 0.25s;
    cursor: not-allowed;
}

@media(max-width: 1350px){
    .shop-feature-cards h4{
        font-size: 1.5rem;
    }

    .shop-feature-cards p{
        font-size: 0.75rem;
    }

    .shop-feature-cards div{
        font-size: 1.5rem;
    }
}

@media(max-width: 1000px){
    .shop-feature-cards h4{
        font-size: 1rem;
    }

    .shop-feature-cards p{
        font-size: 0.75rem;
    }

    .shop-feature-cards div{
        font-size: 1.5rem;
    }
}


@media(max-width: 650px){
    .shop-feature-cards div{
        font-size: 1rem;
    }

}