.product-body{
    width: 100%;
    height: 160vh;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.product-first-half{
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-first-half .boxed-images{
    width: 22%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    gap: 1rem;
}

.product-first-half .boxed-images-eb{
    display: none;
    width: 22%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    gap: 1rem;
}

.product-first-half .product-image{
    width: 45%;
    height: 100%;
}

.product-first-half .product-description{
    width: 33%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 2.2rem;
}

.product-mobile{
    display: hidden;
}

.product-image{
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-container{
    width: 90%;
    height: 70%;
    background-color: #F3F3F3;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-container img{
    height: 70%;
}

.product-description-colors{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.all-colors{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 1rem;
}

.product-description-header h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 3rem;

}

.product-description-header p{
    font-family: "noto sans", sans-serif;
    font-size: 1.25rem;
}

.product-description-price{
    font-family: "lato", sans-serif;
    font-size: 1.5rem;
}

.product-description-colors p{
    font-family: "noto sans", sans-serif;
    font-size: 1rem;
}

.bag-button{
    font-family: "lato", sans-serif;
    padding: 0.75rem;
    width: 70%;
    color: white;
    background-color: black;
    border-radius: 25px;
}

.bag-button:hover{
    color: black;
    background-color: white;
    cursor: not-allowed;
    transition: 0.25s;
}

.product-features{
    width: 70%;
    height: 25vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.product-features hr{
    width: 25rem;
    height: 1px;
    background-color: grey;
    border: none;
}

.product-second-half{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
}

.product-second-half h2{
    font-family: "lato", sans-serif;
    font-size: 2.5rem;
}

.product-first-icons{
    display: flex;
    justify-self: center;
    align-items: center;
    gap: 5rem;
}

.product-second-icons{
    display: flex;
    justify-self: center;
    align-items: center;
    gap: 5rem;
}

@media(max-width: 1300px){
    .product-second-half h2{
        font-size: 1.5rem;
    }

    .product-image .product-container img{
        height: 50%;
    }
}

@media(max-width: 850px){
    .product-body{
        height: 250vh;
    }

    .product-first-half{
        height: 190vh;
        gap: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

    }

    .product-first-half .boxed-images{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        width: 100%;
        height: 10%;
    }


    .product-first-half .boxed-images-eb{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        width: 100%;
        height: 10%;
    }


    .product-second-half h2{
        font-size: 1rem;
    }

    .product-first-half .product-image{
        height: 40%;
        width: 100%;
    }

    .product-first-half .product-image .product-container{
        width: 70%;
        height: 100%;
    }

    .product-body .product-first-half .product-description .product-description-header h2{
        font-size: 1.5rem;
    }

    .product-body .product-first-half .product-description {
        height: 40%;
        width: 100%;
        display: flex;
        flex-wrap: wrap; 
        flex-direction: row;
      }
      
    
      .product-body .product-first-half .product-description > * {
        flex-basis: 50%; 
      }
      
}

@media(max-width: 700px){
    .product-second-half h2{
        text-align: center;
        font-size: 0.85rem;
    }
    .product-first-icons{
        gap: 2rem;
    }
    .product-second-icons{
        gap: 2rem;
    }

    .product-body .product-image .product-container img{
        height: 40%;
    }

    .product-body .product-image .product-container{
        height: 80%;
    }
}

@media(max-width: 500px){
    .product-second-half{
        gap: 0.5rem;
    }

    .product-first-icons{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 0.1rem;
    }
    .product-second-icons{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 0.1rem;
    }
    .product-body .product-image .product-container{
        height: 60%;
    }

    .product-body .product-image{
        height: 30%;
    }

}

@media(max-height: 700px){
    .product-first-half{
        height: 200vh;
    }

    .product-body .product-first-half .product-description{
        height: 60%;
        gap: 0.5rem;
    }



}