.company-body{
    width: 100%;
    height: 130vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 4rem;
}

.company-body-header{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.company-body-header h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.company-body-header p{
    font-family: 'Noto Sans', sans-serif;
    font-size: 1.5rem;
}

.company-body-hero{
    width: 80%;
    background-image: url("../img/about-us-bg.png");
    height: 80vh;
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 2rem;
    padding: 3rem;
    color: white;
}

.company-body-hero h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.company-body-hero p{
    font-family: 'Noto Sans', sans-serif;
    font-size: 1.2rem;
}

.company-body-buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.about-us-button{
    padding:0.75rem;
    width: 10rem;
    background-color: transparent;
    color: white;
    border: 1px solid white;
    font-family: "lato", sans-serif;
}

.about-us-button:hover{
    cursor: pointer;
    color: black;
    background-color: white;
    transition: 0.25s;
}

.terms-button:hover{
    cursor: pointer;
    color: black;
    background-color: white;
    transition: 0.25s;
}

.terms-button{
    padding: 0.75rem;
    width: 11rem;
    background-color: transparent;
    color: white;
    border: 1px solid white;
    font-family: "lato", sans-serif;
}

@media(max-width: 767px){
    .company-body-header{
        text-align: center;
    }

    .company-body-header h2{
        font-size: 3rem;
    }

    .company-body-header p{
        font-size: 1rem;
    }
}