.shop-cards{
    width: 100%;
    height: 50vh;
    display: flex;
}

.shop-cards .image{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.image .image-container{
    height: 100%;
    width: 55%;
    background-color: #ECECEC;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
}

.description{
    width: 55%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 5rem;
}

.description-container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 3rem;
}

.description-container .description-header{
    font-family: 'Bebas Neue', sans-serif;   
}

.description-container .description-header h2{
    font-size: 2.5rem;
}

.description-container .description-header h4{
    font-size: 1.5rem;
}

.description-container ul{
    padding-left: 1rem;
    font-family: 'Noto Sans', sans-serif;

}

.shop-cards .image img{
    width: 15rem;
}

.view-details{
    color: white;
    background-color: black;
    padding: 1rem;
    width: 10rem;
    border-radius: 30px;
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 1.2rem;
}

.view-details:hover{
    color: black;
    background-color: white;
    transition: 0.25s;
    cursor: pointer;
}  

@media(max-width: 1050px){
    .shop-cards .image img{
        width: 10rem;
    }
}

@media(max-width: 850px){
    .shop-cards{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 80vh;
        gap: 1rem;
    }

    .shop-cards .image{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .description{
        padding: 0rem;
    }

    .image .image-container{
        width: 100%;
    }
}

@media(max-width: 475px){
    .shop-cards{
        height: 85vh;
        gap: 0.25rem;
    }

    .shop-cards .image img{
        width: 6rem;
    }

    .view-details{
        padding: 0.75rem;
        font-size: 1rem;
    }
}
