.mailing-list{
    width: 100%;
    height: 50vh;
    background-color: black;
    color: white; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
    font-family: "lato", sans-serif;
}

#email{
    color: white;
    background-color: black;
    border: 1px solid white;
    padding: 1rem;
    width: 25rem;
    border-radius: 15px;
    outline: none;
}

#submit{
    color: black;
    background-color: white;
    padding: 1rem;
    width: 7rem;
    border-radius: 15px;
    font-family: "lato", sans-serif;
}

#submit:hover{
    color: black;
    background-color: red;
    cursor: not-allowed;
    transition: 0.25s;
}

@media(max-width: 500px){
    #email{
        width: 12rem;
    }

    .mailing-list{
        font-size: 0.75rem;
    }
}