.featured-picks{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ECECEC;
    gap: 2rem;
}

.featured-picks .featured-showcase{
    width: 100%;
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
}

.featured-picks h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 4rem;
}

.featured-picks .keep-exploring-button{
    font-family: 'Bebas Neue', sans-serif;   
    color: white;
    background-color: black;
    padding: 1rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    font-size: 1.5rem;
    border-radius: 15px;
}

.featured-picks .keep-exploring-button:hover{
    color: black;
    background-color: white;
    cursor: pointer;
    transition: 0.3s;
}

@media(max-width: 650px){
    .featured-picks h2{
        font-size: 2.4rem;
    }
}