.shop-body{
    height: 280vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10rem;
}

.wireless-headphones{
    width: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
}

.wireless-headphones h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 4rem;

}

.wireless-earbuds{
    width: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
}

.wireless-earbuds h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 4rem;
}

@media(max-width: 850px){
    .shop-body{
        height: 400vh;
    }
}

@media(max-width: 475px){
    .shop-body{
        height: 450vh;
    }
    .wireless-headphones .hp-header{
        font-size: 2.75rem;
    }

    .wireless-earbuds .eb-header{
        font-size: 2.75rem;
    }
}