.information-cards {
    width: 25%;
    height: 90%;
    border-radius: 10px;
    background-color: white;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    font-family: 'Bebas Neue', sans-serif;   
}

.information-cards .header h3{
    font-size: 2rem;
}

.information-cards .header p{
    font-size: 1.25rem
}

.information-cards:hover{
    box-shadow: 5px 5px 10px grey;
    height: 91%;
    width: 26%;
    transition: 0.3s;
    cursor: default;
}

.information-cards img{
    height: 23rem;
}


@media(max-width: 1450px){
    .information-cards img{
        height: 15rem;
    }
    
    .information-cards{
        height: 80%;
        width: 23%;
    }

    .information-cards:hover{
        height: 81%;
    }
}

@media(max-width: 850px){
    .information-cards img{
        height: 10rem;
    }

    .information-cards .header h3{
        font-size: 1.5rem;
    }
    
    .information-cards .header p{
        font-size: 1rem
    }

    .information-cards{
        height: 70%;
    }

    .information-cards:hover{
        height: 71%;
    }
    
}

@media(max-width: 550px){
    .information-cards img{
        height: 5rem;
    }

    .information-cards .header h3{
        font-size: 0.75rem;
    }
    
    .information-cards .header p{
        font-size: 0.5rem
    }

    .information-cards{
        height: 50%;
        width: 23%;
    }

    .information-cards:hover{
        height: 51%;
    }
}