.download-cards{
    width: 80%;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.download-cards-description{
    width: 30%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
}

.download-cards-image{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.download-cards-image img{
    height: 100%;
    width: 70%;
}

.download-cards-buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;

}

.download-cards-description h2{
    background-color: black;
    color: white;
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 2.5rem;
}

.download-cards-description{
    font-family: 'Noto Sans', sans-serif;
}

.download-cards-description ul{
    padding-left: 1rem;
}


.download-now{
    padding: 0.75rem;
    border-radius: 30px;
    width: 10rem;
    font-family: "lato", sans-serif;
    color: white;
    background-color: black;
}

.learn-more{
    padding: 0.75rem;
    border-radius: 30px;
    width: 10rem;
    font-family: "lato", sans-serif;
    color: black;
    background-color: white;
}

.download-now:hover{
    color: black;
    background-color: white;
    cursor:not-allowed;
    transition: 0.25s;
}

.learn-more:hover{
    color: white;
    background-color: black;
    cursor:not-allowed;
    transition: 0.25s;
}

@media(max-width: 1250px){
    .download-cards-image{
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    .download-cards-image img{
        height: 70%;
    }
}

@media(max-width: 1000px){
    .download-cards{
        display: flex;
        justify-self: center;  
        align-items: center;
        flex-direction: column-reverse;
        height: 150vh;
        width: 100%;
    }

    .download-cards-image{
        width: 100%;
    }

    .download-cards-image img{
        width: 100%;
    }

    .download-cards .download-cards-description{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 70%;
    }

    .download-cards .download-cards-description ul{
        text-align: left;
    }

    .download-cards .download-cards-description .download-cards-buttons{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}

@media(max-width: 750px){
    .download-cards .download-cards-description h2{
        font-size: 2rem;
    }
}