.download-body{
    width: 100%;
    height: 150vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
}

.download-body-header{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
}

.download-body-header h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.download-body-header p{
    font-family: 'Noto Sans', sans-serif;
    font-size: 1.5rem;
}

@media(max-width: 1000px){
    .download-body{
        height: 350vh;
    }

    .download-body .download-body-header{
        text-align: center;
    }
}

@media(max-width: 750px){
    .download-body .download-body-header h2{
        font-size: 3rem;
    }

    .download-body .download-body-header p{
        font-size: 1rem;
    }
}