.contact-body{
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 7rem;
}

.contact-body-header{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.contact-body-cards{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
}

.contact-body-header h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.contact-body-header p{
    font-family: 'Noto Sans', sans-serif;
    font-size: 1.5rem;
}

@media(max-width: 650px){
    .contact-body{
        height: 120vh;
    }

    .contact-body-cards{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .contact-body-header h2{
        font-size: 2.5rem;
    }

    .contact-body-header p{
        font-size: 1rem;
    }
}