.support-cards{
    width: 30%;
    height: 35vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
    font-family: "lato", sans-serif;
    border: 1px solid black;
    padding: 5rem;
}

.support-cards-top{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.support-cards-top p{
    font-family: 'Noto Sans', sans-serif;

}

.download-button{
    padding: 1rem;
    width: 10rem;
    color: white;
    background-color: black;
    border-radius: 30px;
    font-family: "lato", sans-serif;
}

.download-button:hover{
    color: black;
    background-color: white;
    cursor: pointer;
    transition: 0.25s;
}

@media(max-width: 1100px){

    .support-cards .support-cards-top{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .support-cards-icon{
        height: 5rem;
    }

    .download-button{
        font-size: 0.65rem;
        width: 7rem;
        padding: 0.4rem;
    }

    .support-cards-text h2{
        text-align: center;
        font-size: 1rem;
    }

    .support-cards-text p{
        text-align: center;
        font-size: 0.75rem;
    }
}

@media(max-width: 700px){
    .support-cards-icon{
        height: 2.5rem;
    }
}

@media(max-width: 650px){
    .support-cards{
        width: 70%;
    }
}