.featured-cards {
    width: 25%;
    height: 90%;
    border-radius: 10px;
    background-color: white;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    font-family: 'Bebas Neue', sans-serif;   
}

.featured-cards .header h3{
    font-size: 2rem;
}

.featured-cards .header p{
    font-size: 1.25rem
}

.featured-cards:hover{
    box-shadow: 5px 5px 10px grey;
    height: 91%;
    width: 26%;
    transition: 0.5s;
}

.featured-cards img{
    width: 17rem;
}

@media(max-width: 1330px){
    .featured-cards img{
        width: 13rem;
    }

    .featured-cards{
        height: 80%;
    }

    .featured-cards:hover{
        box-shadow: 5px 5px 10px grey;
        height: 81%;
        width: 26%;
        transition: 0.5s;
    }
}

@media(max-width: 1024px){
    .featured-cards img{
        width: 10rem;
    }


    .featured-cards .header h3{
        font-size: 1.5rem;
    }
}

@media(max-width: 768px){
    .featured-cards img{
        width: 8rem;
    }

    .featured-cards{
        height: 70%;
    }

    .featured-cards:hover{
        box-shadow: 5px 5px 10px grey;
        height: 71%;
        width: 26%;
        transition: 0.5s;
    }

}


@media(max-width: 650px){
    .featured-cards img{
        width: 5rem;
    }

    .featured-cards{
        height: 60%;
    }

    .featured-cards:hover{
        box-shadow: 5px 5px 10px grey;
        height: 61%;
        width: 26%;
        transition: 0.5s;
    }


}


@media(max-width: 375px){
    .featured-cards img{
        width: 4rem;
    }

}

