.hp-or-eb{
    width: 100%;
    height: 10vh;
    background-color: #ECECEC;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;
    font-size: 1.5rem;
}

.hp{
    width: 8rem;
    height: 99%;;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    border-radius: 10px;
}

.hp:hover{
    border: 1px solid grey;
    background-color: #D1CCCC;
    cursor: pointer;
    transition: 0.35s;
}

.eb{
    width: 8rem;
    height: 99%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    border-radius: 10px;
}

.eb:hover{
    border: 1px solid grey;
    background-color: #D1CCCC;
    cursor: pointer;
    transition: 0.35s;
}