.navbar{
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar-logo a{
    font-family: 'Satisfy', cursive;
    font-size: 2rem;
    color: #A21515;
    text-decoration: none;
}

.navbar-logo a:hover{
    text-decoration-line: underline;
    cursor: pointer;
}

.navbar-links ul{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}

.navbar-links ul li{
    font-family: 'Lato', sans-serif;
    list-style-type: none;
    font-size: 1.2rem;
}

.navbar-links ul li a{
    text-decoration: none;
    color: black;
    padding: 1rem;
}

.navbar-links ul li a span{
    color: #A21515;
}

.navbar-links ul li a:hover{
    text-decoration-line: underline;
    text-decoration-color: #A21515;
    text-decoration-thickness: 2px;
}


@media(max-width:600px){
    .navbar-links ul li{
        font-size: 0.75rem;
    }

    .navbar-logo a{
        font-size: 1.25rem;
    }

    .navbar-links ul{
        gap: 2rem;
    }
}

@media(max-width:425px){
    .navbar-links ul li{
        font-size: 0.75rem;
    }

    .navbar-logo a{
        font-size: 1.25rem;
    }

    .navbar-links ul{
        gap: 0.25rem;
    }
}