.support-body{
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
}

.support-body-cards{
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.support-body-header{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.support-body-header h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.support-body-header p{
    font-family: "Noto Sans", sans-serif;
    font-size: 2rem;
}

@media(max-width: 1100px){
    .support-body-cards{
        gap: 1rem;
    }
}

@media(max-width: 650px){
    .support-body{
        height: 150vh;
    }

    .support-body-cards{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .support-body-header h2{
        font-size: 2.5rem;
    }
    .support-body-header p{
        font-size: 1rem;
    }
}