.service-body{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
}

.service-body-header{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.service-body-header h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.service-body-header p{
    font-family: 'Noto Sans', sans-serif;
    font-size: 1.5rem;
}

.service-body-cards{
    width: 100%;
    height: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
}

.service-button{
    padding: 1rem;
    width: 20rem;
    font-family: "lato", sans-serif;
    color: white;
    background-color: black;
    font-size: 1.25rem;
}

.service-button:hover{
    background-color: white;
    color: black;
    cursor: not-allowed;
    transition: 0.25s;
}


@media(max-width: 1023px){
    .service-body{
        height: 205vh;
    }

    .service-body-cards{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        height: 140vh;
    }

    .service-body-header{
        text-align: center;
    }

    .service-body-header h2{
        font-size: 3rem;
    }

    .service-body-header p{
        font-size: 1rem;
    }

    .service-button{
        font-size: 1rem;
        padding: 0.75rem;
        width: 15rem;
    }
}