.landing-hero {
    width: 100%;
    height: 85vh;
    background-color: #C4B8B8;
    display: flex;
}

.landing-hero .header {
    height: 100%;
    width: 35%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-left: 2rem;
    font-family: 'Dhurjati', sans-serif;
    gap: 1rem;
}

.landing-hero .header h4 {
    font-size: 2rem;
}

.landing-hero .header .new {
    color: #F20505;
}

.landing-hero .header h2 {
    font-family: 'Bebas Neue', sans-serif;
    font-size: 6rem;
}

.landing-hero .display {
    width: 65%;
    height: 100%;
    flex: 1;
    display: flex;
    justify-self: center;
    align-items: center;
    flex-direction: column;
}

.display .heading {
    width: 100%;
    height: 35%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.display .heading h2 {
    font-size: 6rem;
    font-family: 'Bebas Neue', sans-serif;
    color: white;
}

.display .images {
    width: 100%;
    height: 65%;
    display: flex;
    justify-content: center;
    align-items: flex-start;

}

.headphone1 {
    width: 9rem;
    margin-right: 4rem;
}

.headphone2 {
    width: 20rem;
}

.headphone3 {
    width: 30rem;
}

.landing-hero .header .buttons .shop-button {
    font-family: 'Dhurjati', sans-serif;
    background-color: black;
    color: white;
    font-size: 25px;
    border-radius: 50px;
    padding: 5px;
    padding-right: 40px;
    padding-left: 40px;
    margin-top: 30px;
}

.landing-hero .header .buttons .explore-button {
    font-family: 'Dhurjati', sans-serif;
    background-color: #C4B8B8;
    color: black;
    font-size: 25px;
    border-radius: 50px;
    padding: 5px;
    padding-right: 40px;
    padding-left: 40px;
    margin-top: 30px;
    margin-left: 20px;
}

.landing-hero .header .buttons .shop-button:hover {
    background-color: #DB1111;
    transition: 0.8s;
    cursor: pointer;
}

.landing-hero .header .buttons .explore-button:hover {
    background-color: #DB1111;
    color: white;
    transition: 0.8s;
    cursor: pointer;
}

@media(max-width: 1440px) {
    .headphone1 {
        width: 7rem;
        transition: 0.25s;
    }

    .headphone2 {
        width: 15rem;
        transition: 0.25s;
    }

    .headphone3 {
        width: 23rem;
        transition: 0.25s;
    }
}

@media(max-width: 1024px) {
    .headphone1 {
        width: 4rem;
        transition: 0.25s;
    }

    .headphone2 {
        width: 10rem;
        transition: 0.25s;
    }

    .headphone3 {
        width: 18rem;
        transition: 0.25s;
    }

    .landing-hero .display .heading h2 {
        font-size: 4rem;
    }

    .landing-hero .header h4 {
        font-size: 1.25rem;
    }

    .landing-hero .header h2 {
        font-size: 4.5rem;
    }

    .landing-hero .header .buttons .shop-button {
        font-size: 15px;
    }

    .landing-hero .header .buttons .explore-button {
        font-size: 15px;
    }
}

@media(max-width: 768px) {
    .headphone1 {
        width: 3.5rem;
        transition: 0.25s;
    }

    .headphone2 {
        width: 9rem;
        transition: 0.25s;
    }

    .headphone3 {
        width: 13rem;
        transition: 0.25s;
    }

    .landing-hero .display .heading h2 {
        font-size: 4rem;
    }

    .landing-hero .header h4 {
        font-size: 1.25rem;
    }

    .landing-hero .header h2 {
        font-size: 3.5rem;
    }

    .landing-hero .header .buttons .shop-button {
        font-size: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .landing-hero .header .buttons .explore-button {
        font-size: 15px
    }

}

@media(max-width: 650px){
    .landing-hero{
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
    }

    .landing-hero .header{
        width: 100%;
        height: 60%;
        margin: 0;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        gap: 1rem;
        padding: 4rem;
    }

    .landing-hero .display{
        width: 100%;
        height: 40%;
        padding: 1rem;
    }

    .headphone1 {
        width: 2rem;
        transition: 0.25s;
    }

    .headphone2 {
        width: 5rem;
        transition: 0.25s;
    }

    .headphone3 {
        width: 9rem;
        transition: 0.25s;
    }

    .landing-hero .display .heading h2 {
        font-size: 2rem;
    }

    .landing-hero .header h4 {
        font-size: 1rem;
    }

    .landing-hero .header h2 {
        font-size: 2rem;
    }

    .landing-hero .header .buttons{
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

    }

    .landing-hero .header .buttons .shop-button {
        font-size: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .landing-hero .header .buttons .explore-button {
        font-size: 10px;
        width: 9rem;
    }

}


@media(max-width: 375px){

    .landing-hero .header .buttons .explore-button {

        width: 10rem;
    }

    .landing-hero .header{
        padding: 3rem;
    }
}



@media(max-width: 320px){


    .landing-hero .header{
        padding: 1rem;
    }
}

