.product-icons{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: "Noto Sans", sans-serif;
    font-size: 1.2rem;
}

@media(max-width: 1300px){
    .product-icons{
        font-size: 1rem;
    }
}

@media(max-width: 850px){
    .product-icons{
        font-size: 0.7rem;
    }

    .product-icons-icon{
        height: 2rem;
    }
}

@media(max-width:700px){
    .product-icons{
        font-size: 0.5rem;
    }

    .product-icons-icon{
        height: 1.5rem;
    }
}
