.about-body{
    width: 100%;
    height: 145vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 5rem;
}

.about-body h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.about-body-header{
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url("../img/about-bg.png");
    background-position: center;
    background-size: cover;
    color: white;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 4rem;
    padding-left: 2rem;
}

.about-body-header h2{
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 5rem;
}

.about-body-header-text{
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.about-body-header-text p{
    font-family: 'Noto Sans', sans-serif;
    font-size: 0.9rem;
}

@media(max-width: 1023px){
    .about-body h2{
        font-size: 3rem;
    }

    .about-body-header h2{
        font-size: 3rem;
    }
}

@media(max-width: 767px){
    .about-body h2{
        text-align: center;
    }

    .about-body-header-text{
        width: 60%;
    }

    .about-body-header h2{
        font-size: 1.5rem;
        text-align: left;
    }

    .about-body-header-text p{
        font-size: 0.75rem;
    }
}