.shop-feature{
    width: 100%;
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.shop-feature .header{
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-family: 'Bebas Neue', sans-serif;   
    font-size: 2.5rem;
}

.shop-feature-showcase{
    width: 100%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

@media(max-width: 1000px){
    .shop-feature-showcase{
        gap: 2rem;
    }

}

@media(max-width: 650px){
    .shop-feature .header h2{
        font-size: 2.5rem;
    }
}